.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;

	img[class*="wp-image-"] {
		.center-block;
	}

	.wp-caption-text {
		margin: 0.8075em 0;
	}
}

.wp-caption-text {
	text-align: center;
}

.metaslider {
    .caption {
        padding: 1em !important;
        &-wrap {
            background-color: @black !important;
            color: @white !important;
        }
    }
}